
#portfolio {

	.container {

		width: 100%;

		// Removing extra space in the last row of items. Only works for chrome.
		margin-bottom: -24px;
		overflow: hidden;

		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
		align-content: flex-start;

		padding-right: 8px;

		.item {

			position: relative;
			margin-bottom: 24px;

			&.one {

				flex-basis: 100%;

				@include sm {

					flex-basis: calc(25% - 18px); } }

			&.two {

				flex-basis: 100%;

				@include sm {

					flex-basis: calc(50% - 12px); } }

			img {

				width: 100%;

				@include sm {

					position: absolute;
					height: 100%; } } } } }
