
#overlay {

	position: fixed;
	z-index: 9999999;

	display: block;

	width: 100vw;
	height: 100vh;

	background: white;

	opacity: 1;

	transition: .35s opacity ease-in-out;

	&.gone {

		opacity: 0; } }



#background {

	position: fixed;
	top: 50%;
	left: 50%;

	transform: translate(-50%, -50%);

	z-index: -1000;
	background: radial-gradient(ellipse at center, #ffffff 45%,#c7c7c7 70%,#787878 93%);

	pointer-events: none; }

#stars {

	position: fixed;
	z-index: 500;

	width: 100vw;
	height: 100vh;

	pointer-events: none;

	div {

		position: absolute;
		background-image: url("../img/star.png");
		background-size: 100%;

		animation: roll 12s linear infinite;

		&.small {

			width: 15px;
			height: 15px; }

		&.medium {

			width: 25px;
			height: 25px; }

		&.large {

			width: 50px;
			height: 50px; } }

	@keyframes roll {

		0% {

			transform: rotate(0deg); }

		100% {

			transform: rotate(360deg); } } }
