
$headerHeightMb: 30px;
$headerHeightSm: 46px;
$headerHeightMd: 46px;
$headerHeightLg: 46px;
$headerHeightUl: 64px;

header {

	position: absolute;
	z-index: 500;

	ul.nav {

		margin: 0;

		height: $headerHeightMb;
		line-height: $headerHeightMb;

		transition: .5s opacity ease-in-out;

		&.fadeout {

			opacity: 0; }

		@include sm {

			display: block;
			height: $headerHeightSm;
			line-height: $headerHeightSm; }

		@include ul {

			height: $headerHeightUl;
			line-height: $headerHeightUl; }

		li {

			display: none;
			float: left;

			height: inherit;
			line-height: inherit;

			font-size: 19px;

			margin: 0;
			margin-right: 24px;

			cursor: pointer;

			@include sm {

				display: block; }

			@include ul {

				font-size: 24px;
				margin-right: 36px; }

			&.logo {

				display: block !important;
				margin-right: 30px;

				@include ul {

					margin-right: 44px; }

				img {

					display: block;

					width: auto;
					height: 100%; } } } }



	.langSwitch {

		$langSwitchPadding: 4px;

		$langSwitchButtonSizeMb: $headerHeightMb - ($langSwitchPadding * 2);
		$langSwitchBoxWidthMb: ($langSwitchButtonSizeMb * 2) + ($langSwitchPadding * 3);

		$langSwitchButtonSizeSm: $headerHeightSm - ($langSwitchPadding * 2);
		$langSwitchBoxWidthSm: ($langSwitchButtonSizeSm * 2) + ($langSwitchPadding * 3);

		$langSwitchButtonSizeUl: $headerHeightUl - ($langSwitchPadding * 2);
		$langSwitchBoxWidthUl: ($langSwitchButtonSizeUl * 2) + ($langSwitchPadding * 3);

		position: absolute;
		top: 0;
		right: 0;

		width: $langSwitchBoxWidthMb;
		height: $headerHeightMb;

		background: $background-colour;
		border-radius: $langSwitchPadding * 1.5;

		@include sm {

			width: $langSwitchBoxWidthSm;
			height: $headerHeightSm; }

		@include ul {

			width: $langSwitchBoxWidthUl;
			height: $headerHeightUl; }

		.box {

			display: flex;
			align-items: center;
			justify-content: center;

			position: absolute;
			top: $langSwitchPadding;

			width: $langSwitchButtonSizeMb;
			height: $langSwitchButtonSizeMb;

			color: white;
			font-size: .7em;

			@include sm {

				font-size: .9em;

				width: $langSwitchButtonSizeSm;
				height: $langSwitchButtonSizeSm; }

			@include ul {

				width: $langSwitchButtonSizeUl;
				height: $langSwitchButtonSizeUl; }

			&.left {

				left: $langSwitchPadding; }

			&.right {

				right: $langSwitchPadding; }

			&.btn {

				background: $primary-colour;
				border-radius: $langSwitchPadding;

				left: $langSwitchButtonSizeMb + ($langSwitchPadding * 2);

				cursor: pointer;

				transition: .35s all ease-in-out;

				@include sm {

					left: $langSwitchButtonSizeSm + ($langSwitchPadding * 2); }

				@include ul {

					left: $langSwitchButtonSizeUl + ($langSwitchPadding * 2); }

				&:hover {

					background: $primary-colour-highlighted; }

				&:after {

					content: "";

					position: absolute;

					width: 100%;
					height: 100%;

					background-image: url("../img/arrow.png");
					background-position: center;
					background-repeat: no-repeat;
					background-size: 50%;

					transition: .35s all ease-in-out; }

				&.switched {

					left: $langSwitchPadding;

					&:after {

						transform: rotate(180deg); } } } } } }





main {

	width: 100%;
	height: 100%;

	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;

	transition: .5s all ease-in-out;

	#home, #brands, #cv, #portfolio, #contact {

		opacity: 0;
		pointer-events: none;

		overflow-x: hidden;
		overflow-y: auto;

		position: absolute;

		transition: .35s opacity ease-in-out;

		&.horizontal {

			$defMarginVW: 8vw;

			margin: $defMarginVW;

			width: calc(100vw - (#{$defMarginVW} * 2));

			// (100% Height - Default page margin for both sides) - Header height with extra spacing
			height: calc((100vh - (#{$defMarginVW} * 2)) - (#{$headerHeightMb} + #{$defaultGap}));

			// margin-top takes extra space because of the header height
			margin-top: calc(#{$defMarginVW} + (#{$headerHeightMb} + #{$defaultGap}));
			margin-bottom: $defMarginVW;
			margin-left: $defMarginVW;
			margin-right: $defMarginVW;

			@include sm {

				height: calc((100vh - (#{$defMarginVW} * 2)) - (#{$headerHeightSm} + #{$defaultGap}));
				margin-top: calc(#{$defMarginVW} + (#{$headerHeightSm} + #{$defaultGap})); }

			@include ul {

				height: calc((100vh - (#{$defMarginVW} * 2)) - (#{$headerHeightUl} + #{$defaultGap}));
				margin-top: calc(#{$defMarginVW} + (#{$headerHeightUl} + #{$defaultGap})); } }

		&.vertical {

			// same logic applies for vertical spacing

			$defMarginVH: 8vh;

			margin: $defMarginVH;

			width: calc(100vw - (#{$defMarginVH} * 2));

			// (100% Height - Default page margin for both sides) - Header height with extra spacing
			height: calc((100vh - (#{$defMarginVH} * 2)) - (#{$headerHeightMb} + #{$defaultGap}));

			// margin-top takes extra space because of header height
			margin-top: calc(#{$defMarginVH} + (#{$headerHeightMb} + #{$defaultGap}));
			margin-bottom: $defMarginVH;
			margin-left: $defMarginVH;
			margin-right: $defMarginVH;

			@include sm {

				height: calc((100vh - (#{$defMarginVH} * 2)) - (#{$headerHeightSm} + #{$defaultGap}));
				margin-top: calc(#{$defMarginVH} + (#{$headerHeightSm} + #{$defaultGap})); }

			@include ul {

				height: calc((100vh - (#{$defMarginVH} * 2)) - (#{$headerHeightUl} + #{$defaultGap}));
				margin-top: calc(#{$defMarginVH} + (#{$headerHeightUl} + #{$defaultGap})); } } }



	.active {

		opacity: 1 !important;
		pointer-events: auto !important; }

	&.disabled {

		#home, #brands, #cv, #portfolio, #contact {

			pointer-events: none !important; } }

	&.fadeout {

		opacity: 0;

		#home, #brands, #cv, #portfolio, #contact {

			pointer-events: none !important; } } }
