
*, *:before, *:after {

	box-sizing: inherit;
	outline: none; }

iframe, h1, h2, h3, h4, h5, h6, p, a, code, em, img, strong, b, u, i, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed,  figure, figcaption, footer, header, hgroup,  menu, nav, section, summary, time, mark, audio, video {

	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	user-select: none;
	user-drag: none;
	-webkit-user-drag: none;
	-webkit-touch-callout: none; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {

	display: block; }

html {

	height: 100%; }

body {

	width: 100%;
	height: 100%;
	margin: 0;

	color: $primary-text-colour;

	font-family: $primary-font-family;
	font-size: $primary-font-size;
	font-weight: $primary-font-weight;

	line-height: 1;
	overflow: hidden;

	box-sizing: border-box;
	cursor: default;

	user-select: none; }

h1, h2, h3, h4, h5, h6 {

	line-height: 1.25em;
	margin: 1em 0; }

h1 {

	font-size: 2.25em;
	font-weight: 700; }

h2 {

	font-size: 2em;
	font-weight: 700; }

h3 {

	font-size: 1.7em;
	font-weight: 700; }

h4 {

	font-size: 1.5em;
	font-weight: 700; }

h5 {

	font-size: 1.3em;
	font-weight: 400; }

h6 {

	font-size: 1.125em;
	font-weight: 400; }

p {

	line-height: 1.35em;
	margin: 1.5em 0; }

a {

	color: $primary-text-colour;
	text-decoration: none;
	font-family: $mf-medium;

	&:hover {

		color: $primary-colour-highlighted;
		text-decoration: none; } }

a.btn, button {

	display: block;
	float: left;
	clear: left;

	padding: 0.75em 1.5em;

	border-radius: 0.25em;
	border: 1px solid transparent;

	background: $primary-colour;

	font-size: 0.85em;
	line-height: 1em;
	color: $primary-text-colour-reversed;
	text-decoration: none;

	user-select: none;
	cursor: pointer;

	&:hover {

		background: $primary-colour-highlighted; }

	&.shadowlink {

		background: none;
		color: $primary-colour;
		border: 1px solid $primary-colour;

		&:hover {

			color: $primary-text-colour-reversed;
			background: $primary-colour; } } }

em {

	font-style: italic; }

strong {

	font-weight: 700; }

.accented {

	color: $primary-text-accented-colour; }

.marked {

	color: $primary-text-accented-colour;
	font-weight: 700;
	text-decoration: underline; }

.crossed {

	text-decoration: line-through; }

.mf-thin {

	font-family: $mf-thin;
	font-weight: 400; }

.mf-light {

	font-family: $mf-light;
	font-weight: 400; }

.mf-medium {

	font-family: $mf-medium;
	font-weight: 400; }

.highlighted {

	border-radius: 0.25em;
	padding: 0.1em 0.4em;
	background: $primary-colour;
	color: $primary-text-colour-reversed; }

ol, ul {

	list-style: none;

	margin: 24px 0; }

.listed, .listed {

	margin: 24px 0 24px 16px;
	padding-left: 12px; }

ul.listed {

	list-style: disc; }

ol.listed {

	list-style: decimal; }

li {

	margin: 8px 0; }

table {

	border-collapse: collapse;
	border-spacing: 0; }

.clearfix:after {

	content: "";
	display: table;
	clear: both; }



//	Page Layouts

.fx { 	// Fixed Container

	width: 100%;
	margin: auto;

	@include sm {

		width: ($screen-small - 100px); }

	@include md {

		width: ($screen-medium - 100px); }

	@include lg {

		width: ($screen-large - 100px); } }

.fl { 	// Fluid Container

	width: 100%; }

.row:after {

	content: "";
	display: table;
	clear: both; }

.block {

	float: left; }





#__bs_notify__ {

	opacity: .15; }
