
#brands {

	height: 100%;

	position: relative;

	.container {

		display: block;
		position: relative;

		overflow: auto;
		max-width: 100%;
		max-height: 100%;

		text-align: center;

		@include sm {

			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);

			max-height: 100%; }

		.item {

			display: inline-block;

			width: 100%;
			height: 120px;
			line-height: 120px;

			margin: 0;

			@include sm {

				width: 130px;
				height: 130px;
				line-height: 130px;

				padding: 10px; }

			@include lg {

				width: 160px;
				height: 160px;
				line-height: 160px;

				padding: 20px; }

			@include ul {

				width: 280px;
				height: 280px;
				line-height: 280px; }

			img {

				display: block;

				margin: 0 auto;

				width: auto;
				height: 100%; } } } }
