
@font-face {

	font-family: "Montserrat-Thin";
	font-weight: 400;
	src: url("fonts/montserrat-thin.woff2") format("woff2"), url("fonts/montserrat-thin.woff") format("woff"); }

@font-face {

	font-family: "Montserrat-ExtraLight";
	font-weight: 400;
	src: url("fonts/montserrat-extralight.woff2") format("woff2"), url("fonts/montserrat-extralight.woff") format("woff"); }

@font-face {

	font-family: "Montserrat-Medium";
	font-weight: 400;
	src: url("fonts/montserrat-medium.woff2") format("woff2"), url("fonts/montserrat-medium.woff") format("woff"); }

//	Media Queries
//	Compiles in the same order as declared here by the combiner plugin.

$screen-small: 768px;
$screen-medium: 900px;
$screen-large: 1200px;
$screen-ultra: 1921px;

@mixin sm {

	@media (min-width: $screen-small) {

		@content; } }

@mixin md {

	@media (min-width: $screen-medium) {

		@content; } }

@mixin lg {

	@media (min-width: $screen-large) {

		@content; } }

@mixin ul {

	@media (min-width: $screen-ultra) {

		@content; } }

@mixin mbMax {

	@media (max-width: ($screen-small - 1)) {

		@content; } }

@mixin smMax {

	@media (max-width: ($screen-medium - 1)) {

		@content; } }

@mixin mdMax {

	@media (max-width: ($screen-large - 1)) {

		@content; } }

@mixin lgMax {

	@media (max-width: ($screen-ultra - 1)) {

		@content; } }



// Default Styles

$primary-colour: #00d1ff;
$primary-colour-highlighted: #00d1ff;
$primary-text-colour: black;
$primary-text-accented-colour: black;
$primary-text-colour-reversed: white;
$primary-font-family: "Montserrat-ExtraLight", Arial, Helvetica, sans-serif;
$primary-font-size: 16px;
$primary-font-weight: 400;
$primary-line-height: 1.25em;

$background-colour: #202020;

$defaultGap: 20px;


// mf = Montserrat Font

$mf-thin: "Montserrat-Thin";
$mf-light: "Montserrat-ExtraLight";
$mf-medium: "Montserrat-Medium";
