
$cvGap: 90px;

#cv {

	overflow: auto;

	padding-right: 8px;

	.block {

		float: left;
		width: 100%;

		font-size: 0.85em;

		&:nth-child(1) {

			@include md {

				width: 100%;

				padding-right: ($cvGap / 2); }

			@include lg {

				width: 33.33%; } }

		&:nth-child(2) {

			@include md {

				width: 50%;

				padding-left: ($cvGap / 4);
				padding-right: ($cvGap / 4); }

			@include lg {

				width: 33.33%; } }

		&:nth-child(3) {

			@include md {

				width: 50%;

				padding-left: ($cvGap / 2); }

			@include lg {

				width: 33.33%; } }



		h3 {

			margin: 30px 0;
			font-family: $mf-medium; }

		p {

			line-height: 1.75em; }

		a {

			font-size: 16px; }

		.list {

			float: left;
			width: 100%;

			@include sm {

				width: 50%; }

			h4 {

				display: inline-block;

				margin: 0;
				font-size: 1.25em;

				border-bottom: 1px solid black; }

			ul {

				line-height: 1.25em; } }

		.job {

			height: 80px;
			margin: 20px 0;

			.info {

				position: relative;
				top: 50%;
				transform: translateY(-50%);

				float: left;
				width: 65%;

				.company {

					font-family: $mf-medium;
					font-size: 1.2em;

					margin: 6px 0; }

				.title {

					margin: 6px 0; }

				.timeframe {

					margin: 6px 0;
					font-size: 0.8em; } }

			.logo {

				float: right;
				width: 30%;
				height: inherit;

				position: relative;

				img {

					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%); }

				img.theking {

					width: auto;
					height: 80%; }

				img.noluyo {

					width: 70%;
					height: auto; }

				img.empire {

					width: auto;
					height: 50%; } } }

		.school {

			margin-top: 20px;
			line-height: 1.25em;

			border-bottom: 1px dotted black;

			&:last-of-type {

				border-bottom: none; }

			.name {

				font-family: $mf-medium;
				font-size: 1.25em; }

			.container {

				margin: 12px 0 20px;

				&:after {

					content: "";
					display: table;
					clear: both; }

				.type {

					float: left;
					width: 60%; }

				.timeframe {

					float: right;
					width: 30%;
					text-align: right; } } }

		.seperator {

			width: 100%;
			height: 1px;

			clear: both; } } }
